// Animation Keyframes - by Chaney

@keyframes heartbeat {
  0% {
    transform: scale( 1 );
  }
  25% {
    transform: scale( 1.4 );
  }
  40% {
    transform: scale( 1 );
  }
  75% {
    transform: scale( 1.6 );
  }
  100% {
    transform: scale( 1 );
  }
}
@keyframes zoomfade {
  0% {
    transform: scale( 1.7 );
    opacity: 0;
  }
  65% {
    transform: scale( 0.9 );
    opacity: 1;
  }
  70% {
    transform: scale( 1 );
    opacity: 1;
  }
  85% {
    transform: scale( 1.1 );
    opacity: 0.3;
  }
  100% {
    transform: scale( 1 );
    opacity: 1;
  }
}
